<template>
  <div>
    <!-- Modal -->
    <div
      class="modal fade"
      id="exampleModal"
      tabindex="-1"
      aria-labelledby="exampleModalLabel"
      aria-hidden="true"
    >
      <div class="modal-dialog modal-dialog-centered">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title" id="exampleModalLabel">
              <div>
                <p>{{ objinfo.name }}</p>
                <p>## {{ objinfo.mark }} ##</p>
              </div>
            </h5>
            <button
              type="button"
              class="close"
              data-dismiss="modal"
              aria-label="Close"
            >
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <p>售價 {{ sizestr }}</p>
            <p>甜度</p>
            <div class="input-group">
              <div class="input-group-prepend">
                <div class="input-group-text" v-for="item in sweet" :key="item">
                  <label :for="item.id">
                    <input
                      type="radio"
                      name="sweet"
                      aria-label="Radio button for following text input"
                      v-model="sweetchoose"
                      :value="item"
                      :id="item.id"
                    />
                    <span>{{ item }}</span>
                  </label>
                </div>
              </div>
            </div>
            <p>冰塊</p>
            <div class="input-group">
              <div class="input-group-prepend">
                <div class="input-group-text" v-for="item in ice" :key="item">
                  <label :for="item.id">
                    <input
                      type="radio"
                      name="ice"
                      aria-label="Radio button for following text input"
                      v-model="icechoose"
                      :value="item"
                      :id="item.id"
                    />
                    <span>{{ item }}</span>
                  </label>
                </div>
              </div>
            </div>
            <p>規格 / 數量</p>
            <div class="sizecount row align-items-center">
              <div class="col d-flex">
                <div class="form-group mr-4 mb-0">
                  <select
                    class="form-control"
                    v-model="sizechoose"
                    id="exampleFormControlSelect1"
                  >
                    <option value="中杯">中杯</option>
                    <option value="大杯">大杯</option>
                  </select>
                </div>

                <div class="mr-3" role="group" aria-label="Basic example">
                  <button
                    type="button"
                    class="btn btn-danger"
                    @click="count--"
                    :disabled="boola"
                  >
                    -
                  </button>
                  <button type="button" class="btn text-dark" disabled>
                    {{ count }}
                  </button>
                  <button
                    type="button"
                    class="btn btn-success"
                    @click="count++"
                    :disabled="boolb"
                  >
                    +
                  </button>
                </div>
                <span class="align-self-end"> $ {{ totalprice }}</span>
              </div>
            </div>
          </div>
          <div class="modal-footer">
            <span class="text-danger" v-if="msgcontrol">請選擇"{{ msg }}"</span>
            <span class="text-success" v-if="msgsuccess">成功加入</span>
            <button
              type="button"
              class="btn btn-info"
              @click="joinshop"
              :disabled="joinbtnctl"
            >
              加入購物車
            </button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Modal",
  data() {
    return {
      sweet: ["無糖", "微糖", "半糖", "少糖", "正常"],
      ice: ["去冰", "微冰", "少冰", "正常"],
      sweetchoose: "",
      icechoose: "",
      sizechoose: "中杯",
      price: 0,
      count: 1,
      sizestr: "",
      msg: "",
      msgcontrol: false,
      msgsuccess: false,
    };
  },
  props: {
    gettomodal: {
      Type: Object,
      default() {
        return {
          id: 1,
          name: "",
          mark: "",
          size: {
            mid: "",
            big: "",
          },
        };
      },
    },
  },
  created() {},
  methods: {
    joinshop() {
      const sweet = "甜度 ";
      const ice = "冰塊";
      this.msg = "";
      if (this.sweetchoose == "" && this.icechoose == "") {
        this.msg += sweet + ice;
        this.msgcontrol = true;
        this.msgsuccess = false;
        console.log(1);
        return;
      } else if (this.sweetchoose != "" && this.icechoose == "") {
        this.msg += ice;
        this.msgcontrol = true;
        this.msgsuccess = false;
        console.log(2);
        return;
      } else if (this.sweetchoose == "" && this.icechoose != "") {
        this.msg += sweet;
        this.msgcontrol = true;
        this.msgsuccess = false;
        console.log(3);
        return;
      } else {
        this.msgsuccess = true;
        this.msgcontrol = false;
        console.log(4);
      }
      this.$store.commit("pushshopcar", {
        name: this.objinfo.name,
        size: this.sizechoose,
        sweet: this.sweetchoose,
        ice: this.icechoose,
        count: this.count,
        price: this.totalprice,
      });
    },
    clearchoose() {
      console.log(5);
      this.msg = "";
      this.msgsuccess = false;
      this.msgcontrol = false;
      this.sweetchoose = "";
      this.icechoose = "";
      this.count = 1;
      this.sizechoose = "中杯";
    },
  },
  computed: {
    objinfo() {
      return this.gettomodal;
    },
    totalprice() {
      if (this.sizechoose == "中杯") {
        return this.count * this.objinfo.size.mid;
      } else {
        return this.count * this.objinfo.size.big;
      }
    },
    joinbtnctl() {
      if (this.objinfo.size.big == "" && this.sizechoose == "大杯") {
        return true;
      } else {
        return false;
      }
    },
    boola() {
      return this.count <= 1 ? true : false;
    },
    boolb() {
      return this.count >= 5 ? true : false;
    },
    shopjsonlength() {
      return this.$store.state.shopcar.length;
    },
  },
  watch: {
    objinfo(obj) {
      let mid = obj.size.mid || "";
      let big = obj.size.big || "";
      this.sizestr = big == "" ? "中" + mid : "中" + mid + " / " + "大" + big;
    },
    // shopjsonlength() {
    //   this.clearchoose();
    // },
  },
};
</script>

<style lang="scss">
.modal-header {
  border-bottom: none;
  .modal-title {
    p {
      font-size: 34px;
      letter-spacing: 2px;
      font-weight: 500;
      margin-bottom: 3px;
      &:nth-child(2) {
        font-size: 16px;
      }
    }
  }
}
.modal-body {
  .input-group {
    margin-bottom: 20px;
    label {
      margin-bottom: 0;
    }
  }
  p {
    font-size: 18px;
    font-weight: 300;
    letter-spacing: 2px;
    margin-left: 3px;
  }
  .sizecount {
    .col {
      width: auto;
      padding-left: 20px;
      .form-control {
        width: 80px;
      }
    }
  }
}

@media (max-width: "576px") {
  .input-group-text {
    font-size: 14px;
    padding: 4px;
  }
}
</style>


